*{
  padding: 0;
  margin: 0;
}

.App{
  display: -ms-flexbox;
  display: flex;
  padding: 5%;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  position: absolute;
}
.Grid{
  width: 420px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 400px;
}
.color{
  position: absolute;
  top: 0;
  left: 0;
}
.container{
  height: 400px;
  width: 110px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-left: 20px;
}

.block_container{
  -webkit-transition: -webkit-transform .1s ease;
  transition: -webkit-transform .1s ease;
  -o-transition: transform .1s ease;
  transition: transform .1s ease;
  transition: transform .1s ease, -webkit-transform .1s ease;
  -webkit-transform-origin:100% 100%;
      -ms-transform-origin:100% 100%;
          transform-origin:100% 100%;/*鼠标的位置在3/3的位置*/
  height: 108px;
  width: 110px;
}

.score{
  font-size: 20px;
  text-align: center;
  width: 100%;
  position: fixed;
  top: 10px;
}

.block_cell{
  width: 20px;
  height: 20px;
  margin-right: 2px;
  border-radius: 2px;
  display: inline-block;
}

.cell{
  width: 9.2%;
  height: 9.2%;
  background: #eee;
  display: inline-block;
  padding: 0;
  margin: 2px 2px 0 0;
  border-radius: 2px;
}
.color .cell{
  z-index: 10;
  background-color: transparent;
}
@media screen and (max-width:420px){
  .App{
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .container{
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    height: 110px
  }
}


